*, *::before, *::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  font-family: var(--FF);
  background: var(--Dark-BG);
  color: var(--Dark-Text2);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  margin-bottom: 0.1875rem;
  font-size: 1.5rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --LimeGreen: hsl(163, 72%, 41%);
  --BrightRed: hsl(356, 69%, 56%);
  --Facebook: hsl(208, 92%, 53%);
  --Twitter: hsl(203, 89%, 53%);
  --Instagram-strt: hsl(37, 97%, 70%);
  --Instagram-End: hsl(329, 70%, 58%);
  --YouTube: hsl(348, 97%, 39%);
  --Toggle-strt: hsl(210, 78%, 56%);
  --Toggle-End: hsl(146, 68%, 55%);
  --Toggle-BGC: hsl(230, 22%, 74%);
  --Dark-BG: hsl(230, 17%, 14%);
  --Dark-Top-BG: hsl(232, 19%, 15%);
  --Dark-Card-BG: hsl(228, 28%, 20%);
  --Dark-Text1: hsl(228, 34%, 66%);
  --Dark-Text2: hsl(0, 0%, 100%);
  --Light-BG: hsl(0, 0%, 100%);
  --Light-Top-BG: hsl(225, 100%, 98%);
  --Light-Card-BG: hsl(227, 47%, 96%);
  --Light-Text1: hsl(228, 12%, 44%);
  --Light-Text2: hsl(230, 17%, 14%);
}

:root {
  --FF: "Inter", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.75rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--Dark-Text1);
}
.toggle {
  position: relative;
  border: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.5rem;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=system] {
  grid-column: 2/3;
  grid-row: 2/3;
  justify-self: center;
}
.toggle label[for=light] {
  grid-column: 3/4;
}
.toggle__wrapper {
  position: relative;
  height: 1.5rem;
  grid-column: 2/3;
  grid-row: 1/2;
  margin: 0 auto;
}
.toggle input[type=radio] {
  margin: 0;
  opacity: 0;
}
.toggle__background {
  top: 0;
  border-radius: 0.75rem;
  width: 100%;
  position: absolute;
  height: 100%;
  display: block;
  background: linear-gradient(225deg, var(--Toggle-strt) 0%, var(--Toggle-End) 98.02%);
}
.toggle__button {
  position: absolute;
  top: 0.1875rem;
  left: 0.1875rem;
  border-radius: 50%;
  width: 1.125rem;
  height: 1.125rem;
  background-color: var(--Dark-BG);
  transition: all 159ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #dark :checked ~ .toggle__button {
  left: 3px;
}

.card {
  padding: 1.25rem;
}